import Endpoint from '@/services/Endpoint'

export default {
  partyClasses (payload) {
    return Endpoint().get('/s1/nucleus/partyClassification', {
      params: payload
    })
  },
  createPartyClass (payload) {
    return Endpoint().post('/s1/nucleus/partyClassification', payload)
  },
  modifyPartyClass (payload) {
    return Endpoint().patch('/s1/nucleus/partyClassification', payload)
  }
}
