<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center mb-base">
      <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      <vs-button class="p-3 mr-4" @click="modify" color="primary" type="filled" icon="fa-pen" icon-pack="far" v-if="$acl.check('isNucleusAll')">{{ $t('MODIFY') }}</vs-button>
    </div>
    <div class="row hover">
      <p class="info-row">
        <span>{{ $t('ID') }}:</span>
        <span>{{ partyClass.partyClassificationId }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('CLASS_NAME') }}:</span>
        <span>{{ partyClass.description | placeholder('-') }}</span>
      </p>
      <p class="info-row" v-if="partyClass.parentClassificationId">
        <span>{{ $t('PARENT_CLASS') }}:</span>
        <span><router-link :to="{ name: 'ViewPartyClass', params: { id: partyClass.parentClassificationId } }" :title="partyClass.parentClassificationDescription">{{ partyClass.parentClassificationDescription }}</router-link></span>
      </p>
      <p class="info-row">
        <span>{{ $t('CLASS_TYPE') }}:</span>
        <span>{{ partyClass.classificationTypeDescription }}</span>
      </p>
      <p class="info-row">
        <span>{{ $t('STANDARD_CODE') }}:</span>
        <span>{{ partyClass.standardCode | placeholder('-') }}</span>
      </p>
    </div>
  </nv-card>
</template>

<script>
import PartyClassService from '@/services/nucleus/PartyClassService'

export default {
  name: 'party-class-view',
  data: function () {
    return {
      partyClass: {},
      popupActive: false
    }
  },
  methods: {
    async load () {
      let self = this
      PartyClassService.partyClasses({
        partyClassificationId: this.$route.params.id
      }).then(function (response) {
        self.partyClass = response.data.classificationList[0] || {}
      })
    },
    cancel () {
      this.$router.back()
    },
    modify () {
      this.$router.push({ name: 'ModifyPartyClass', params: { id: this.$route.params.id } })
    }
  },
  mounted () {
    this.load()
  },
  watch: {
    $route (to, from) {
      this.load()
    }
  }
}
</script>
